<template>
  <footer :style="footerStyle">
    <div class="text" :style="textStyle">
    Made with ❤ by Jason Yu &copy; 2019
    </div>
    <label class="insane-mode-label">
      <input type="checkbox" v-model="insaneMode"> Insane Mode (new!)
    </label>
  </footer>
</template>

<script>
const randomNumber = (min, max) => min + (Math.random() * (max - min));
const randomPercentage = () => `${randomNumber(0, 100)}%`;
const randomHex = (min, max) => Math.round(randomNumber(min, max)).toString(16);
const randomColor = () => '#' + Array.from({ length: 6 }, () => randomHex(0, 16)).join('');

const FOOTER_INTERVAL_MS = 543;
const randomFooterStyle = (insaneFactor) => ({
  borderRadius: `${randomPercentage()} ${randomPercentage()} / ${randomPercentage()} ${randomPercentage()}`,
  background: randomColor(),
  transitionDuration: `${FOOTER_INTERVAL_MS / insaneFactor}ms`,
});

const TEXT_INTERVAL_MS = FOOTER_INTERVAL_MS / 3;
const randomTextStyle = (insaneFactor) => ({
  transform: `rotate(${randomNumber(-3 * insaneFactor, 3 * insaneFactor)}deg) scale(${randomNumber(0.7 * insaneFactor, 1.3 * insaneFactor)})`,
  color: randomColor(),
  transitionDuration: `${TEXT_INTERVAL_MS / insaneFactor}ms`,
});

export default {
  mounted () {
  },

  beforeDestroy () {
    window.clearInterval(this.footerInterval);
    window.clearInterval(this.textInterval);
  },

  data: () => ({
    footerStyle: randomFooterStyle(),

    textStyle: randomTextStyle(),

    insaneMode: false,
  }),

  computed: {
    insaneFactor () {
      return this.insaneMode ? 3 : 1;
    },
  },

  watch: {
    insaneMode: {
      immediate: true,
      handler () {
        window.clearInterval(this.footerInterval);
        window.clearInterval(this.textInterval);

        this.setUpFooterInterval();
        this.setUpTextInterval();
      },
    },
  },

  methods: {
    setUpFooterInterval() {
      const { insaneFactor } = this;
      const intervalMs = FOOTER_INTERVAL_MS / insaneFactor;
      this.footerInterval = window.setInterval(() => {
        this.footerStyle = randomFooterStyle(insaneFactor)
      }, intervalMs);
    },

    setUpTextInterval() {
      const { insaneFactor } = this;
      const intervalMs = TEXT_INTERVAL_MS / insaneFactor
      this.textInterval = window.setInterval(() => {
        this.textStyle = randomTextStyle(insaneFactor)
      }, intervalMs);
    },
  },
};
</script>













<style scoped>
footer {
  margin-top: 1rem;
  padding: 3rem 0;
  transition-property: border-radius, background;
  text-align: center;
}

footer .text {
  transition-property: color, transform;
}

.insane-mode-label {
  display: block;
  margin-top: 2rem;
}
</style>
